.go-back {
    display: flex;
    width: 130px;
    justify-content: center;
    font-size: 18px;
    cursor: pointer;
    color: white;
    background-color: transparent;
    border: 2px solid white;
    border-radius: 10px;
}

.go-back:hover {
    background-color: white;
    color: #272b30;
}
.button-wrapper {
    display: flex;
    width: 100%;
    justify-content: flex-start;
    margin-left: 7%;
}
